.feedback-form {
  &::before {
    content: "";
    position: absolute;
    height: 1px;
    background-color: $grey-150;
    bottom: top;
    left: -20px;
    right: -20px;
  }

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 50px;

    &::before {
      content: none;
    }
  }
}

.feedback-form__item {
  position: relative;
  border: none;
  padding: 0;
  margin: 0;

  &:nth-last-child(3) {
    margin-bottom: 50px;

    @media (min-width: $tablet-width) {
      margin-bottom: 8px;
    }
  }

  &--first {
    @media (min-width: $tablet-width) {
      order: -1;
      border-right: 1px solid $grey-500;
    }
  }

  &--span2 {
    @media (min-width: $tablet-width) {
      grid-column: span 2;
    }
  }

  &:not(:nth-last-child(3))::before {
    content: "";
    position: absolute;
    height: 1px;
    background-color: $grey-150;
    bottom: 0;
    left: -20px;
    right: -20px;
  }

  @media (min-width: $tablet-width) {
    &::before {
      display: none;
    }
  }
}

.feedback-form__interpretation {
  display: none;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 26px;

  @media (min-width: $tablet-width) {
    display: block;
    justify-self: end;
  }
}

.feedback-form__button {
  padding: 18.5px 32px 13.5px;
  width: 100%;
  text-align: center;
}
